import axios from 'axios';

const api = axios.create({
    baseURL: 'https://vivek-lime.vercel.app',
    headers: {
        'Content-Type': 'application/json',
        Accept: 'application/json'
    }
})


export const getQuestion = async () => api.get('/api/question/get');
export const getAbilityScore = async (score) => api.get(`/api/report/ability?score=${score}`);
export const getWillingnessScore = async (score) => api.get(`/api/report/ability?score=${score}`);
